import React, { Component } from 'react';
import { Row, Col, Form, Select, Checkbox } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import FilterPathEducate from 'components/common/input/FilterPathEducate';

const { Option } = Select;

@Form.create({ name: 'formScholarshipApproval' })
class YearlyClass extends Component {
  state = {};

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;

    return (
      <Form className="ant-advanced-search-form" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={8}>
            <Form.Item label="Học kỳ">
              {getFieldDecorator('hk', {})(
                <Select>
                  <Option value="1">02</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Năm học">
              {getFieldDecorator('nh', {})(
                <Select>
                  <Option value="1">2014-2015</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Lần thi">
              {getFieldDecorator('lt', {})(
                <Select>
                  <Option value="1">01</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Học phần">
              {getFieldDecorator('hp', {})(
                <Select>
                  <Option value="1">Công tác văn thư và lưu trữ</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Điểm của học kỳ">
              {getFieldDecorator('dhk', {})(
                <Select>
                  <Option value="1">02</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Năm học">
              {getFieldDecorator('nh', {})(
                <Select>
                  <Option value="1">2014-2015</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Sắp xếp theo">
              {getFieldDecorator('sxt', {})(
                <Select>
                  <Option value="1">Họ và tên</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} classes={{ isShow: false }} />
        </Row>
        <Row gutter={24}>
          <Col span={8}>
            <Form.Item>
              {getFieldDecorator('sxt')(<Checkbox>Hiển thị tất cả sinh viên</Checkbox>)}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item>
              {getFieldDecorator('sxt')(<Checkbox>Tính lại tất cả điểm cho sinh viên</Checkbox>)}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch />
              <BtnClear />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default YearlyClass;



